<template>
    <div class="house-list-page">
        <div class="build">
            <div class="build-discode-tree">
                <el-input placeholder="输入行政区关键字" size="small" v-model="filterText" clearable></el-input>
                <div style="height:calc(100% - 42px); padding: 10px 0;">
                    <happy-scroll size="10" resize>
                        <el-tree ref="tree" class="dis-code" :data="disCodeOption" :default-expanded-keys="isShowCode" node-key="code" accordion :filter-node-method="filterNode"
                        @check-change="handleCheckChange" check-strictly show-checkbox></el-tree>
                    </happy-scroll>
                </div>
            </div>
            <div class="build-list-header">
                <techo-icon class="build-icon" name="list-ul" />
                <span>楼栋列表</span>
                <div class="build-tool">
                    <div class="build-tool-item" @click="resetForm">
                        <techo-icon name="plus" />
                        <span>增加</span>
                    </div>
                </div>
            </div>
            <div class="build-list-table">
                <el-input placeholder="输入楼栋名称关键字" size="small" v-model="buildName" :disabled="editCheckId == ''" clearable></el-input>
                <div class="build-list">
                    <happy-scroll size="10" resize>
                        <div v-if="buildData.length === 0" class="build-item" style="justify-content: center;">暂无数据</div>
                        <div v-else class="build-item" v-for="(item, index) in buildData" :key="index" @click="handleBuildQueryHouse(item)">
                            <div class="xuhao">{{ index+1 }}</div>
                            <div class="name" :title="item.name">{{ item.name }}</div>
                            <el-button size="mini" type="text" @click.stop="handleDeleteBuild(item)">删除</el-button>
                        </div>
                    </happy-scroll>
                </div>
            </div>
            <div class="techo-list__pager">
                <el-pagination ref="pager" @current-change="handlePagerChange" :pager-count="5" :page-size="10"
                    layout="total, prev, pager, next" :total="recordCount">
                </el-pagination>
            </div>
        </div>
        <div class="house">
            <div class="build-list-header">
                <techo-icon class="build-icon" name="wpforms" />
                <span>楼栋信息</span>
            </div>
            <div class="house-form">
                <el-form ref="buildForm" :model="buildForm" :rules="rules" label-width="100px">
                    <el-row :gutter="15">
                        <el-col :span="8">
                            <el-form-item label="楼栋名称" prop="name">
                                <el-input v-model="buildForm.name" :maxlength="25" placeholder="小区名称+楼号；示例:幸福小区1号楼"></el-input>
                            </el-form-item>  
                            <el-form-item label="单元数" prop="unitCount">
                                <el-input v-model="buildForm.unitCount" :maxlength="10" placeholder="请填写单元数"></el-input>
                            </el-form-item>  
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="楼栋类型" prop="type">
                                <el-select placeholder="请选择楼栋类型" clearable v-model="buildForm.type">
                                    <el-option label="楼房" :value="1"></el-option>
                                    <el-option label="平房" :value="2"></el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item label="层数" prop="floorCount">
                                <el-input v-model="buildForm.floorCount" :maxlength="10" placeholder="请填写层数"></el-input>
                            </el-form-item>  
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label-width="30px">
                                <techo-position ref="position" :code="editCheckId" :placement="'bottom'" :width="'500'"
                                :openType="'building'" :buttonName="'选择楼栋编码'" @onBuildInfo="handleBuildCode"></techo-position>
                                <div style="display: inline-flex;margin-left: 10px;">{{ buildForm.buildingCode }}</div>
                            </el-form-item>  
                        </el-col>
                    </el-row>
                </el-form>
                <div class="footer-tool">
                    <div class="form-footer">
                        <el-button type="warning" size="small" @click="resetForm">重置</el-button>
                        <el-button type="success" size="small" @click="submitForm">{{ isUpdate == true ? "更新" : "保存" }}</el-button>
                    </div>
                </div>
                <div class="house-table">
                    <div class="build-list-header">
                        <techo-icon class="build-icon" name="list-ul" />
                        <span>房列表</span>
                        <el-popover v-if="buildInfo.id != undefined" v-model="visibleHousePopover" placement="right" trigger="click" @hide="handleHideHousePopover">
                            <div style="padding: 10px 0;color: #f00;font-size: 18px;">注意：批量生成房信息时请确保此楼栋此单元中没有房信息！！！</div>
                            <div style="display: -webkit-flex;">
                                <div style="color: #f00;margin-right: 5px;">*</div><div>单元</div>
                            </div>
                            <el-checkbox-group style="padding: 10px 0;" v-model="unitCheckList">
                                <el-checkbox v-for="(item, index) in buildInfo.unitCount" :label="index+1" :key="index">{{ index+1 }} 单元</el-checkbox>
                            </el-checkbox-group>
                            <div v-if="unitCheckList.length == 0" style="color: #f90000;padding-bottom: 10px;">请选择单元</div>
                            <div style="display: -webkit-flex;">
                                <div style="color: #f00;margin-right: 5px;">*</div><div>每层房数</div>
                            </div>
                            <div style="padding: 10px 0;">
                                <el-input-number v-model="floorHouseNum" size="mini" :step="2"></el-input-number>
                            </div>
                            <div v-if="floorHouseNum <= 0" style="color: #f90000;padding-bottom: 10px;">请填写每层房数</div>
                            <el-button size="mini" type="primary" style="float: right;" @click="handleSaveHouse">生成</el-button>
                            <div style="float: right;cursor: pointer;" slot="reference">
                                <techo-icon style="margin-right: 10px;" name="plus" />
                                <span>生成房信息</span>
                            </div>
                        </el-popover>
                    </div>
                    <!-- 房信息组件 -->
                    <vHouseInfo ref="vHouseInfo" :buildInfo="buildInfo"></vHouseInfo>
                </div>
            </div>
        </div>
        <el-dialog class="house-table-dialog" title="房信息列表" :visible.sync="houseListVisible" :close-on-click-modal="false" :close-on-press-escape="false">
            <div class="table-info">请先删除楼栋下的房信息！</div>
            <el-table ref="houseTable" size="medium" :data="houseDataList" border highlight-current-row tooltip-effect="dark"
                stripe height="100%" style="width: 100%" @selection-change="handleSelectionChange" @row-click="handleRowClick"
                header-cell-class-name="techo-list__body-table-header">
              <el-table-column type="selection" width="40" align="center"></el-table-column>
              <el-table-column label="房主姓名" prop="householderName" align="center"></el-table-column>
              <el-table-column label="房主电话" prop="householderPhone" align="center"></el-table-column>
              <el-table-column label="房号" prop="houseNum" align="center"></el-table-column>
              <el-table-column label="单元" prop="unit" align="center"></el-table-column>
              <el-table-column label="层数" prop="floor" align="center"></el-table-column>
              <el-table-column label="房屋类型" prop="type" align="center">
                  <template slot-scope="scope">
                      <span>{{ scope.row.type == 1 ? "楼房" : "平房" }}</span>
                  </template>
              </el-table-column>
              <el-table-column label="是否有关联信息" fixed="right" align="center" width="120">
                <template slot-scope="scope">
                    <el-tag :type="checkSelectable(scope.$index,scope.row)?'success':'danger'"></el-tag>
                    <span>{{ checkSelectable(scope.$index,scope.row) ? '是':'否' }}</span>
                </template>
              </el-table-column>
            </el-table>
            <div slot="footer" class="dialog-footer">
                <el-button type="danger" size="small" @click="handleDeleteHouse" :disabled="tableSelection.length == 0">删除房信息</el-button>
            </div>
        </el-dialog>
        <techo-details v-if="dlgVisible" ref="details" :configKey="configKey" :height="'60%'"></techo-details>
    </div>
</template>

<script>
import service from "./services/build_info_service.js";
import vHouseInfo from "./house-info.vue";

export default {
  components: {
    vHouseInfo
  },
  data() {
    return {
      title: "房信息",
      isUpdate: false, // 当前表单是否为修改楼栋信息表单
      buildData: [],
      isShowCode: ["6102"], // 数默认展开第一级
      page: 1,
      pageSize: 10,
      recordCount: 0,
      buildForm: {
        name: "",
        type: "",
        buildingCode: "",
        unitCount: "",
        floorCount: "",
        districtCode: []
      },
      buildInfo: {}, // 当前选中的楼栋信息
      rules: {
        name: [
          { required: true, message: "请输入楼栋名称", trigger: "blur" },
          { min: 2, max: 25, message: "长度在 2 到 25 个字符", trigger: "blur" }
        ],
        type: [
          { required: true, message: "请选择楼栋类型", trigger: "change" }
        ],
        unitCount: [
          { required: true, message: "请输入层数", trigger: "blur" },
          util.validate.valiInteger()
        ],
        floorCount: [
          { required: true, message: "请输入层数", trigger: "blur" },
          util.validate.valiInteger()
        ]
      },
      disCodeOption: [],
      filterText: "",
      editCheckId: "", // 树选中节点
      buildName: "", // 楼栋名称
      // 详情页面是否显示
      dlgVisible: false,
      configKey: "houseView",

      houseListVisible: false, // 房列表是否显示
      houseDataList: [], // 房数据
      tableSelection: [], // 房列表选中的数据
      deleteBuildInfo: {}, // 需要删除的楼栋的信息

      unitCheckList: [], // 需要批量生成房信息的单元
      floorHouseNum: 0, // 每层房数
      visibleHousePopover: false, // 批量生成房信息面板是否显示
    };
  },

  watch: {
    filterText(val) {
      this.$refs.tree.filter(val);
    },
    buildName(val) {
      this.handleQuery();
    }
  },

  mounted() {
    service.requestGridCode(data => {
      this.disCodeOption = [data];
      this.isShowCode = [data.code];
    });
  },

  methods: {
    /** 选择楼栋编码 */
    handleBuildCode(val) {
        this.buildForm.buildingCode = val.CODE;
    },
    /** 批量生成房 */
    handleSaveHouse() {
      if (this.unitCheckList.length != 0 && this.floorHouseNum > 0) {
        let that = this;
        let param = [];
        this.$popup.showLoading("正在批量添加房信息，请稍候...");
        _.map(that.unitCheckList, unit => {
          for (let floor = 1; floor <= that.buildInfo.floorCount; floor++) {
            for (let houseNum = 1; houseNum <= that.floorHouseNum; houseNum++) {
              param.push({
                buildingId: that.buildInfo.id,
                districtCode: that.buildInfo.districtCode,
                houseNum:
                  unit +
                  "" +
                  (floor < 10 ? "0" + floor : floor) +
                  "" +
                  (houseNum < 10 ? "0" + houseNum : houseNum),
                unit: unit,
                floor: floor
              });
            }
          }
        });
        // 批量创建房信息
        service.requestMadeHouse(param, data => {
          that.$popup.showNotification({
            type: "success",
            message: "房批量添加成功！"
          });
          this.visibleHousePopover = false;
          this.$refs["vHouseInfo"].handleQueryHouse();
          this.$popup.closeLoading();
        });
      }
    },
    /** 关闭生成房单元选择弹出框 */
    handleHideHousePopover() {
      this.unitCheckList = [];
      this.floorHouseNum = 0;
    },
    /** 节点过滤 */
    filterNode(value, data) {
      if (!value) return true;
      return data.label.indexOf(value) !== -1;
    },
    /** 节点被选中 */
    handleCheckChange(data, checked, indeterminate) {
      if (checked == true) {
        this.editCheckId = data.code;
        this.$refs.tree.setCheckedKeys([data.code]);
        this.handleQuery();
        this.resetForm();
      } else {
        if (this.editCheckId == data.code) {
          this.$refs.tree.setCheckedKeys([data.code]);
        }
      }
    },
    handlePagerChange() {
      if (this.$refs["pager"] == undefined) {
        this.page = 1;
        this.pageSize = 10;
      } else {
        this.page = this.$refs["pager"].internalCurrentPage;
        this.pageSize = this.$refs["pager"].internalPageSize;
      }
      this.handleQuery();
    },

    /** 楼栋列表点击行选中 */
    handleBuildQueryHouse(item) {
      $(".build-item").each((index, value) => {
        value.className = "build-item";
      });
      let event = event || window.event;
      event.currentTarget.className += " actived-item";
      this.isUpdate = true;
      this.buildForm = _.cloneDeep(item);
      this.buildInfo = _.cloneDeep(item);
    },
    /** 页码改变后执行的函数 */
    handlePageChange() {
      this.buildData = [];
      this.handleQuery();
    },
    /** 查询楼栋列表 */
    handleQuery(success) {
      let pageInfo = {
        page: this.page,
        pageSize: this.pageSize,
        districtCode: this.editCheckId,
        name: this.buildName
      };
      service.requestPageBuild(pageInfo, data => {
        this.recordCount = data.totalCount;
        this.buildData = data.list;
        success && success();
      });
    },
    /** 保存楼栋信息表单 */
    submitForm() {
      if (this.editCheckId == "") {
        this.$popup.showNotification({
          message: "请先选取行政区！"
        });
      } else {
        this.$refs["buildForm"].validate(valid => {
          if (valid) {
            let param = _.cloneDeep(this.buildForm);
            if (this.isUpdate == false) {
              param.districtCode = this.editCheckId;
              // 添加
              service.requestSaveBuild(param, data => {
                this.$popup.showNotification({
                  type: "success",
                  message: "楼栋信息添加成功！"
                });
                this.resetForm();
                this.handleQuery();
              });
            } else {
              service.requestUpdateBuild(param, data => {
                this.$popup.showNotification({
                  type: "success",
                  message: "楼栋信息修改成功！"
                });
                this.handleQuery(() => {
                  let data = _.cloneDeep(this.buildForm);
                  data.unitCount = Number(data.unitCount);
                  data.floorCount = Number(data.floorCount);
                  this.buildInfo = data;
                });
              });
            }
          } else {
            console.log("error submit!!");
            return false;
          }
        });
      }
    },
    /** 重置楼栋信息表单 */
    resetForm() {
      this.$refs["buildForm"].resetFields();
      this.isUpdate = false;
      this.buildForm = {
        name: "",
        type: "",
        buildingCode: "",
        unitCount: "",
        floorCount: "",
        districtCode: []
      };
      this.buildInfo = {};
      $(".build-item").each((index, value) => {
        value.className = "build-item";
      });
    },
    /** 删除楼栋 */
    handleDeleteBuild(item) {
      new Promise((resolve, reject) => {
        /** 查询楼栋下是否有房信息 */
        service.requestBuildByHouse(
          {
            page: 1,
            pageSize: 1,
            buildingId: item.id
          },
          data => {
            resolve(data);
          }
        );
      }).then(data => {
        if (data.totalCount != 0) {
          this.houseDataList = data.list;
          this.houseListVisible = true;
          this.deleteBuildInfo = item;
        } else {
          this.deleteBuildInfo = {};
          service.requestDeleteBuild(item.id, val => {
            this.handleQuery();
          });
        }
      });
    },
    /** 点击行选中 */
    handleRowClick(row) {
      this.$refs.houseTable.toggleRowSelection(row);
    },
    // 列表选择集改变
    handleSelectionChange(value) {
      this.tableSelection = value;
    },
    /** 房是否有下属关联信息 */
    checkSelectable(index, row) {
      if (
        row.peopleCount != 0 ||
        row.companyCount != 0 ||
        row.placeCount != 0 ||
        row.schoolCount != 0
      ) {
        row.isHoldRelevance = true;
        return true;
      } else {
        return false;
      }
    },
    /** 删除楼栋下房信息 */
    handleDeleteHouse() {
      let that = this;
      let count = _.compact(_.map(this.tableSelection, "isHoldRelevance"))
        .length;
      let messageInfo = "",
        messageSuccess = "";
      if (count != 0) {
        messageInfo = "房下有关联信息，请慎重操作！";
        messageSuccess = "游离数据请到数据关联菜单下重新关联！";
      }
      this.$popup.showConfirm({
        message: "你确定要删除选中房？" + messageInfo,
        successFunc: () => {
          that.$popup.showLoading("正在删除中，请稍候...");
          new Promise((resolve, reject) => {
            let count = 0;
            _.map(that.tableSelection, (item, index) => {
              if (item.isHoldRelevance) {
                // 房 下存在关联信息 修改房数据的楼栋关联
                item.buildingId = null;
                delete item.isHoldRelevance;
                service.requestUpdateHouse(item, data => {
                  count++;
                  count == that.tableSelection.length && resolve();
                });
              } else {
                // 房下没有关联信息 直接删除
                service.requestDeleteHouse(item.id, data => {
                  count++;
                  count == that.tableSelection.length && resolve();
                });
              }
            });
          })
            .then(() => {
              this.handleDeleteBuild(this.deleteBuildInfo);
              this.houseListVisible = false;
              that.$popup.closeLoading();
              that.$popup.showNotification({
                type: "success",
                message: "房删除成功！" + messageSuccess
              });
            })
            .catch(() => {
              that.$popup.showNotification({
                message: "房删除失败！"
              });
              that.$popup.closeLoading();
            });
        }
      });
    }
  }
};
</script>

<style lang="scss">
@import "./styles/house-page.scss";
</style>

