<template>
    <el-dialog class="house-add-dialog" :title="title" :visible.sync="dialogVisible" @close="resetForm">
        <el-form ref="dialogForm" :model="dialogForm" :rules="teamRules" label-width="100px">
            <el-row :gutter="15">
              <el-col :span="8">
                <el-form-item label="房号" prop="houseNum">
                    <el-input v-model="dialogForm.houseNum" :maxlength="12" placeholder="请填写户号"></el-input>
                </el-form-item>  
                <!-- <el-form-item label="户类型">
                    <el-select placeholder="请选择户类型" v-model="dialogForm.type">
                        <el-option v-for="(item, index) in dic['户口类型']" :label="item.name" :value="item.code" :key="index"></el-option>
                    </el-select>
                </el-form-item>   -->
                <el-form-item v-if="isAdd == false" label="单元">
                    <el-input v-model="dialogForm.unit" disabled></el-input>
                </el-form-item>  
              </el-col>
              <el-col :span="8">
                <el-form-item label="房主名称" prop="householderName">
                    <el-input v-model="dialogForm.householderName" :maxlength="12" placeholder="请填写户主名称"></el-input>
                </el-form-item> 
                <el-form-item v-if="isAdd == false" label="层数">
                    <el-input v-model="dialogForm.floor" disabled></el-input>
                </el-form-item>  
              </el-col>
              <el-col :span="8">
                <el-form-item label="房主电话" prop="householderPhone">
                    <el-input v-model="dialogForm.householderPhone" :maxlength="12" placeholder="请填写户主联系方式"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
        </el-form>
        <div slot="footer" class="dialog-footer">
			<el-button type="warning" size="small" @click="resetForm">取消</el-button>
            <el-button type="success" size="small" @click="submitForm">保存</el-button>
		</div>
    </el-dialog>
</template>
<script>
import service from "./services/build_info_service.js";
export default {
  components: {},
  data() {
    return {
      dialogVisible: false,
      isAdd: true,
      title: "",
      dic: {
        户口类型: [],
      },
      dialogForm: {},
      teamRules: {
        houseNum: [
          { required: true, message: "请输入户号", trigger: "blur" },
          { min: 2, max: 12, message: "长度在 2 到 12 个字符", trigger: "blur" }
        ],
        householderName: [
          { required: true, message: "请输入户主姓名", trigger: "blur" }
        ],
        householderPhone: [
          { required: true, message: "请输入户主联系方式", trigger: "blur" },
          util.validate.tellAndPhone()
        ],
        type: [
          { required: true, message: "请选择户类型", trigger: "change" }
        ],
      }
    };
  },

  created () {
    let that = this;
    /** 获取字典表目录 */
    service.requestDicCatalog(data => {
      let dicKeys = _.keys(that.dic);
      _.map(data, item => {
        if (_.indexOf(dicKeys, item.name) != -1) {
          /** 通过字典表目录ID查询字典表 */
          service.requestDicType(item.id, data => {
            that.dic[item.name] = data;
          });
        }
      });
    });
  },
  methods: {
    submitForm() {
      this.$refs["dialogForm"].validate(valid => {
        if (valid) {
          let param = _.cloneDeep(this.dialogForm);
          param.dicMap && delete param.dicMap;
          if (this.isAdd == true) {
            // 添加
            service.requestSaveHouse(param, data => {
              this.$popup.showNotification({
                type: "success",
                message: "户信息添加成功！"
              });
              this.resetForm();
              this.$parent.handleQueryHouse();
            });
          } else if (this.isAdd == false) {
            // 修改
            service.requestUpdateHouse(param, data => {
              this.$popup.showNotification({
                type: "success",
                message: "户信息修改成功！"
              });
              this.resetForm();
              this.$parent.handleQueryHouse();
            });
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    /** 取消 */
    resetForm() {
      this.$refs["dialogForm"].resetFields();
      this.$data.dialogVisible = false;
      this.isAdd = true;
      this.dialogForm = {};
    }
  }
};
</script>
<style lang="scss">
.house-add-dialog {
    .el-date-editor.el-input {
        width: 100%;
    }
    .el-select,.el-cascader {
        width: 100%;
    }
    .el-form {
        .is-disabled .el-input__inner {
            background-color: #ffffff;
            color: #000000;
        }
    }

} 
</style>