<template>
    <div class="house-list__pager">
        <div class="hander-button">
            <el-button class="button-item" plain size="small" v-for="(item , index) in buildInfo.unitCount" :key="index" @click="handleUnit(index+1)">
                <techo-icon class="build-icon" name="check-square" style="margin-right: 5px;" />{{ index+1 }} 单元</el-button>
        </div>
        <div class="bottom-content">
            <el-tabs class="build-floor-tabs" tab-position="left" v-model="queryForm.floorCount">
                <el-tab-pane :label="NumToString(index+1)" :name="String(index+1)" v-for="(item , index) in buildInfo.floorCount" :key="index">
                    <div class="house-item" v-for="(item, index) in houseData" :key="index" @click.stop="handleViewHouse(item)" @mousemove.stop="isShowDel = 'show' + index" @mouseout.stop="isShowDel = ''">
                        <div class="house-name">{{ item.houseNum }}</div>
                        <div class="house-img">
                            <div class="house-img-edit" @click.stop="handleEditHouse(item)" v-if="isShowDel == 'show' + index">
                                <techo-icon name="pencil-square"/>
                            </div>
                            <img class="house_img" :src="item.dicMap.isHoldRelevance == true ? houseImg : houseImg1" />
                            <img class="el-icon-remove" :src="delImg" @click.stop="handleDelHouse(item)" v-if="isShowDel == 'show' + index">
                        </div>
                        <div class="house-num">{{ item.peopleCount }} 人</div>
                    </div>
                    <div class="button-save" @click="handleSaveHouse">
                        <div>+</div>
                    </div>
                </el-tab-pane>
            </el-tabs>
        </div>
        <vViewDialog ref="vViewDialog" :title="title"></vViewDialog>
        <vAddDialog ref="vAddDialog"></vAddDialog>
    </div>
</template>

<script>
import service from "./services/build_info_service.js";
import vViewDialog from "./house-info-view.vue";
import vAddDialog from "./house-info-add.vue";
export default {
  components: {
    vViewDialog,
    vAddDialog
  },
  props: {
    buildInfo: {
      type: Object,
      default() {
        return {
          unitCount: "", // 单元数
          floorCount: "" // 层数
        };
      }
    }
  },
  data() {
    return {
      title: "户信息",
      isShowDel: "",
      queryForm: {
        unitCount: "1", // 默认单元数
        floorCount: "1" // 默认层数
      },
      houseData: [], // 户信息列表
      houseImg: require("./images/1.png"),
      houseImg1: require("./images/2.png"),
      delImg: require("./images/del.png")
    };
  },

  watch: {
    queryForm: {
      handler(value) {
        if (this.buildInfo.id != undefined) {
          this.handleQueryHouse();
        }
      },
      deep: true
    },
    buildInfo() {
      this.$nextTick(() => {
        this.handleUnit(1);
        $(".button-item").each((index, value) => {
          index == 0 && (value.className += " button-active");
        });
        this.queryForm = {
          unitCount: "1",
          floorCount: "1"
        };
      });
    }
  },

  methods: {
    /** 查看户信息 */
    handleViewHouse(item) {
      this.$refs["vViewDialog"].dialogVisible = true;
      this.$refs["vViewDialog"].houseInfo = _.cloneDeep(item);
    },
    /** 修改户信息 */
    handleEditHouse(item) {
      this.$refs["vAddDialog"].dialogVisible = true;
      this.$refs["vAddDialog"].isAdd = false;
      this.$refs["vAddDialog"].title = "修改" + this.title;
      this.$refs["vAddDialog"].dialogForm = _.cloneDeep(item);
    },
    /** 删除户信息 */
    handleDelHouse(item) {
      let that = this;
      let messageInfo = "",messageSuccess = "";
      if(item.dicMap.isHoldRelevance) {
          messageInfo = "户下有关联信息，请慎重操作！";
          messageSuccess = "游离数据请到数据关联菜单下重新关联！"
      };
      this.$popup.showConfirm({
        message: "你确定要删除选中户？" + messageInfo,
        successFunc: () => {
          that.$popup.showLoading("正在删除中，请稍候...");
          new Promise((resolve, reject) => {
            if (item.dicMap.isHoldRelevance == true) {
              // 户 下存在关联信息 修改户数据的楼栋关联
              item.buildingId = null;
              delete item.dicMap;
              service.requestUpdateHouse(item, data => {
                resolve();
              });
            } else {
              // 户下没有关联信息 直接删除
              service.requestDeleteHouse(item.id, data => {
                resolve();
              });
            }
          }).then(() => {
            that.$popup.closeLoading();
            that.$popup.showNotification({
              type: "success",
              message: "户删除成功！" + messageSuccess
            });
            that.handleQueryHouse();
          });
        }
      });
    },
    /** 添加户信息 */
    handleSaveHouse() {
      this.$refs["vAddDialog"].dialogVisible = true;
      this.$refs["vAddDialog"].isAdd = true;
      this.$refs["vAddDialog"].title = "添加" + this.title;
      let dialogForm = {
        buildingId: this.buildInfo.id,
        districtCode: this.buildInfo.districtCode,
        floor: this.queryForm.floorCount,
        unit: this.queryForm.unitCount
      };
      this.$refs["vAddDialog"].dialogForm = dialogForm;
    },
    /** 查询户信息 */
    handleQueryHouse() {
      let param = _.cloneDeep(this.queryForm);
      param.buildingId = this.buildInfo.id;
      service.requestBuildByHouse(param, data => {
        _.map(data.list, item => {
          item.dicMap.isHoldRelevance =
              item.peopleCount != 0 ||
              item.companyCount != 0 ||
              item.placeCount != 0 ||
              item.schoolCount != 0
                ? true
                : false
        });
        this.houseData = data.list;
      });
    },
    /** 点击单元数 */
    handleUnit(value) {
      this.queryForm.unitCount = value;
      $(".button-item").each((index, value) => {
        value.className +=
          "el-button button-item el-button--default el-button--small is-plain";
      });
      let event = event || window.event;
      event.currentTarget.className += " button-active";
    },
    /** 转文字 */
    NumToString(value) {
        return value + "层";
    },
  }
};
</script>

<style lang="scss">
@import "./styles/house-info.scss";
</style>

